import BaseApiService from "../../BaseApiService";

// ---------------- Default path ------------------ //
const DEFAULT_PATH = "/custom/api/qc/";

class QcService extends BaseApiService {
  getDocumentStructure = (loanId: string, applicantUid: string) => {
    const endPoint =
      DEFAULT_PATH + `manage/structure?loanUid=${loanId}&applicantUid=${applicantUid}`;
    return this.makeGetRequestWithAuth(endPoint);
  };

  getDocumentDetails = (search: string) => {
    const endPoint = DEFAULT_PATH + `uid/${search}`;
    return this.makeGetRequestWithAuth(endPoint);
  };
  // /manage/initiate/api/check?qcUid=

  initiateApiCheck = (qcUid: string) => {
    const endPoint = DEFAULT_PATH + `manage/initiate/api/check?qcUid=${qcUid}`;
    return this.makePutRequestWithAuth(endPoint);
  };

  updateQc = (qcUid: string, payload: any) => {
    const endPoint = DEFAULT_PATH + `manage/${qcUid}`;
    return this.makePutRequestWithAuth(endPoint, payload);
  };

  stageComplete = (loanUid: string) => {
    const endPoint = DEFAULT_PATH + `manage/stage/complete/${loanUid}`;
    return this.makePutRequestWithAuth(endPoint);
  };

  collateralRick = (start=0,limit=10,search: string) => {
    const endPoint = DEFAULT_PATH + `manage/collateral/risk${search}`;
    return this.makeGetRequestWithAuth(endPoint);
  };
}

export default new QcService();
